import React from 'react';
// import PropTypes from 'prop-types';
import { Route } from 'react-router-dom';
import styled from 'styled-components';
import data from './data.json'

const StatisticsStyled = styled.div`
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 160px;
  align-content: flex-start;
  justify-items: flex-start;
`;

const Row = styled.div`
  display: inline-block;
`;

const Outter = styled.div`
  /* display: inline-block; */
  position: relative;
  width: 20px
  height: 20px;
  margin: 2px;
  background: #ebedf0;
  outline: 1px solid rgba(27,31,35,.04);
  outline-offset: -1px;
  cursor: pointer;

  &:hover .title{
    display: block;
  }
`;


const Inner = styled.div`
  background: #216e39;
  opacity: ${props=>props.count/5};
  width: 100%;
  height: 100%;
`;

const Title = styled.div`
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  background: black;
  opacity: 0.7;
  color: #eee;
  z-index: 100;
  padding: 0.6em;
  line-height: 1.618em;
  width: 300px;
  font-size: 0.7rem;
`;

const Statistics = () => {
  let Element = data.map(item=>{
    return (
      <Outter>
        <Inner count={item.data.length}/>
        <Title className='title'>
          {item.date}<br/>
          {item.data.map(data=>(
            <React.Fragment>
              {`${data.title}: ${data.content}`}<br/>
            </React.Fragment>
          ))}
        </Title>
      </Outter>
    )
  }
  )
  return (
    <StatisticsStyled>
      {Element}
    </StatisticsStyled>
  );
};

Statistics.propTypes = {
};

export default Statistics;